import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, Input } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { withRouter } from 'react-router'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
@connect(
  state => ({ ...state.batchOrderConfirmDet }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
    this.state = {
      checked: false
    }
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, match = {} } = this.props
    const { params = '' } = match
    const { id = '' } = params
    let reqObj = { ...searchParams, batchId: id }
    handleGetList({ searchParams: { ...reqObj } })
  }
  render() {
    const {
      searchParams = {}
    } = this.props
    const { orderCode = '' } = searchParams
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <div className='commonInput '>
              <label htmlFor=''>工单编码：</label>
              <div className='box '>
                <Input
                  value={orderCode}
                  onChange={e => {
                    const {
                      target: { value = '' }
                    } = e
                    this.onInputChange({ orderCode: value })
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <Row type='flex' justify='start'>
              <Button
                type='primary'
                className={style.mr10}
                onClick={this.query}
              >
                查询
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(withRouter(App))



