import * as actionType from './actionType'
import { getData } from 'util'
import { API_BATCHORDERCONFIRMDETS_PAGE } from 'api'
//同步
export const update = object => ({
  type: actionType.BATCHORDERCONFIRMDET_UPDATE,
  data: object
})

// 获取批量工单确认-详情
export const handleGetList = (reqData, callBack) => dispatch => {
  console.log('reqData is ', reqData)
  const { searchParams = {}, page, size } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  console.log('searchParams is', searchParams)
  getData({
    api: API_BATCHORDERCONFIRMDETS_PAGE,
    ...{ searchParams }
  }).then(data => {
    let { page, size } = reqData.searchParams
    dispatch({
      type: actionType.BATCHORDERCONFIRMDET_UPDATE,
      data: {
          // page,
          // size,
        tableLoading: false,
        total: data.result.total,
        searchResult: data.result || []
      }
    })
  })
    .catch(err => {
      dispatch({
        type: actionType.BATCHORDERCONFIRMDET_FAIL
      })
    })
}



