import React, { PureComponent } from 'react'
import { Table, message } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
import { withRouter } from 'react-router'
import style from './style/Table.scss'
@connect(
  state => ({ ...state.batchOrderConfirmDet, userLoation: state.userLoation }),
  mapDispatchToProps
)
class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
    this.columns = [
      {
        title: '工单编码',
        width: 100,
        dataIndex: 'orderCode',
        key: 'orderCode',
        render: (text, record) => (
            <div>
              {text}
            </div>
        )
      },
      {
        title: '商品编码',
        width: 100,
        dataIndex: 'productCode',
        key: 'productCode'
      },
      {
        title: '商品名称',
        width: 100,
        dataIndex: 'productName',
        key: 'productName'
      },
      {
        title: '规格',
        width: 100,
        dataIndex: 'spec',
        key: 'spec'
      },
      {
        title: '单位',
        width: 60,
        dataIndex: 'unit',
        key: 'unit'
      },
      {
        title: '计划产量',
        width: 60,
        dataIndex: 'planQty',
        key: 'planQty'
      },
      {
        title: '实际产量',
        width: 60,
        dataIndex: 'qty',
        key: 'qty'
      },
      {
        title: '生产日期',
        width: 100,
        dataIndex: 'produceTime',
        key: 'produceTime'
      },
      {
        title: '生产波次',
        width: 80,
        dataIndex: 'batchName',
        key: 'batchName'
      },
      {
        title: '产线',
        width: 100,
        dataIndex: 'produceLine',
        key: 'produceLine'
      },
      {
        title: '状态',
        width: 60,
        dataIndex: 'status',
        key: 'status'
      },
      {
        title: '失败理由',
        width: 100,
        dataIndex: 'cause',
        key: 'cause'
      },
      {
        title: '处理时间',
        width: 100,
        dataIndex: 'updateTime',
        key: 'updateTime'
      }
    ]
  }
  componentDidMount() {
    const { handleGetList, searchParams, match = {}, update } = this.props
    const { params = '' } = match
    const { id = '' } = params
    if (id) {
    //获取详情
      const params = {
        searchParams: {
          batchId: id
        }
      }
      update({
        searchParams: { ...searchParams, batchId: id }
      })
      handleGetList(params)
    }
    console.log('id is', id)
  }
 // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    const newUserLoationValue = newProps && newProps.userLoation && newProps.userLoation.value || ''
    const userLoationValue = userLoation && userLoation.value || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          console.log('reqObj[i] is', reqObj[i])
          reqObj[i] = reqObj[i].value
        }
      }
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  // 导出
  handleExport = () => {
    const { handleExport, searchParams, page, size } = this.props
    handleExport({ searchParams: { page, size, ...searchParams } }, () => {
      message.success('导出成功')
    })
  }
  render() {
    const { total, page, size, tableLoading, searchResult = [] } = this.props
    let pagination = {
      total,
      showTotal: total => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['10', '50', '100', '200', '500']
    }
    return (
      <div className='table-group' style={{ overflowY: 'scroll' }}>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: countTableScroll(this.columns), y: 600 }}
          onChange={this.onChange}
          pagination={false}
          // rowSelection={rowSelection}
        />
      </div>
    )
  }
}
export default (withRouter(app))



