import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import Form from './components/Form'
import Table from './components/Table'
import { connect } from 'react-redux'
import { withRouter } from 'react-router'
import { mapDispatchToProps } from './redux/mapDispatchToProps'

@connect(
  state => ({
    ...state.batchOrderConfirmDet,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
  }
  componentWillUnmount() {
    this.props.update({ searchResult: [], searchParams: {} })
  }
  render() {
    const { history, menuPermissionIds } = this.props
    return (
      <section>
        <Bread route={['生产中心', '批量工单确认详情']} />
        <Form menuPermissionIds={menuPermissionIds} />
        <Table menuPermissionIds={menuPermissionIds} history={history} /></section>
    )
  }
}
export default (withRouter(App))



